import { SidePanel } from 'ui.sidepanel';
import Item from '../item';

export default class EInvoiceApp extends Item
{
	showSlider(): void
	{
		SidePanel.Instance.open(
			'/einvoice/install/',
			{
				width: 575,
				allowChangeHistory: false,
			},
		);
	}

	supportsLayout(): Boolean
	{
		return false;
	}
}
